<!-- 分辨率 900*573 -->
<template>
  <div class="kanban_page">
    <div class="title">设备分布</div>
    <div class="distribute_con">
      <div style="height: 4%">
        <div style="float: left;" >
          <span style="color: aqua;font-weight: bold;margin-right: 10px;font-size: 18px;" >实时状态</span>
          <span>运行数:</span><span class="devcard xystyle" >{{devState.runCount}}</span>
          <span>空闲数:</span><span class="devcard kxstyle" >{{devState.idleCount}}</span>
          <span>离线数:</span><span class="devcard lxstyle" >{{devState.offLineCount}}</span>
          <span>告警数:</span><span class="devcard gjstyle" >{{devState.warnCount}}</span>
        </div>
        <state-label
          class="state_label"
          :colors="['#4A9AE9', '#EEAC41', '#777777', '#E24131']"
        ></state-label>
      </div>
      <div class="device_wra">
        <vue-seamless-scroll
          :data="deviceList"
          :class-option="optionHover"
          class="seamless-warp"
        >
          <ul class="lineWra" ref="lineWra">
            <li
              class="itemLine"
              v-for="line in deviceList"
              :key="line[0][0].deviceId"
            >
              <p class="lineName">{{ line[0][0].lineName }}</p>
              <div class="lineCon" v-for="row in line" :key="row[0].deviceId">
                <div
                  class="itemDevice"
                  :style="{ width: 'calc(100% /' + rowDeviceNum + ')' }"
                  v-for="item in row"
                  :key="item.deviceId"
                >
                  <template v-if="JSON.stringify(item) !== '{}'">
                    <div
                      :class="[
                        'innerState',
                        'state' + (item.deviceRealState || 3)
                      ]"
                    >
                      <p class="deviceName">{{ item.deviceName }}</p>
                      <div><span style="font-size: 1.8rem;" >当前{{ stateFunc(item.deviceRealState) }}持续时间：{{ timeLongFunc(item) }}</span></div>
                      <div><span style="font-size: 1.8rem;" >实时产能：{{ item.todayValue }}</span></div>
                      <div
                        v-for="(param, index) in item.parameter || []"
                        :key="index"
                        class="parameter"
                      >
                        <span v-if="param.parameterValue!== ' ' && param.parameterValue!== ''" class="parameterName">{{ param.parameterName }}:</span>
                        <span v-if="param.parameterValue!== ' ' && param.parameterValue!== ''" style="margin-left: 5px;" >{{ unitFunc(param) }}</span>
                      </div>
                    </div>
                  </template>
                </div>
              </div>
            </li>
          </ul>
        </vue-seamless-scroll>
        <div class="light"></div>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery';
import vueSeamlessScroll from 'vue-seamless-scroll';
import StateLabel from '@/components/common/kanban/StateLabel';
import { fontSize } from '@/utils/common';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import { STATUS_CODE } from '@/utils/constantsFile';
import { deviceRealLiveGetPageByDevice, getServerTime, deviceRealLiveGetDeviceRealState } from '@/api/kanban';
// import $ from 'jquery';
export default {
  name: 'distribute',
  components: {
    StateLabel,
    vueSeamlessScroll
  },
  data() {
    return {
      kanbanConfig: {},
      // 没行展示的设备数
      rowDeviceNum: 5,
      deviceList: [],
      timer: null,
      currentTime: 0,
      devState: {
        runCount: 0,
        idleCount: 0,
        offLineCount: 0,
        warnCount: 0
      },
      dataTime: 0
    };
  },
  computed: {
    optionHover() {
      return {
        step: 1, // 数值越大速度滚动越快
        limitMoveNum: this.limitMoveNum(), // 开始无缝滚动的数据量 this.list.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000 // 单步运动停止的时间(默认值1000ms)
      };
    }
  },
  async created() {
    document.documentElement.style.fontSize = fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = fontSize() + 'px';
    };
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      this.kanbanConfig =
        KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    // this.currentTime = new Date(
    //   $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')
    // ).getTime();
    this.getDataTime();
    this.getDeviceData();
    this.showDevRealState();
    this.timer = setInterval(() => {
      this.getDeviceData();
      this.showDevRealState();
    }, this.kanbanConfig?.time || 15000);
    setInterval(() => {
      this.getDataTime();
    }, 30000);
  },
  destroyed() {
    this.timer && clearInterval(this.timer);
  },
  methods: {
    stateFunc(state) {
      if (state === 1) {
        return '运行';
      } else if (state === 2) {
        return '空闲';
      } else if (state === 3) {
        return '离线';
      } else if (state === 4) {
        return '告警';
      }
    },
    timeLongFunc(param) {
      // var newTime = new Date().getTime();
      var now = (this.dataTime - param.stateStartTime) / 1000 / 60;
      // console.log(now);
      // if (now) {
      if (now < 1) {
        return '1分钟';
      } else if (now > (60 * 24 * 7)) {
        return '始终';
      } else if (now > 1) {
        return this.toHoursAndMinutes(now);
      }
      // } else {
      //   return '<1分钟';
      // }
      // }
      // return now;
    },
    toHoursAndMinutes(totalMinutes) {
      var hours = Math.floor(totalMinutes / 60);
      var minutes = Math.floor(totalMinutes % 60);
      if (hours) {
        return `${hours}小时${minutes > 0 ? ` ${minutes}分` : ''}`;
      } else {
        return `${minutes > 0 ? ` ${minutes}分` : ''}`;
      }
    },
    unitFunc(param) {
      if (param.extend01) {
        var json = JSON.parse(param.extend01);
        return json[param.parameterValue];
      } else {
        return param.parameterValue + ' ' + param.showUnit;
      }
    },
    async showDevRealState() {
      var condtion = {};
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId > 0) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          },
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      try {
        const res = await deviceRealLiveGetDeviceRealState({}, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.devState = {
            runCount: res.rows.runCount,
            idleCount: res.rows.idleCount,
            offLineCount: res.rows.offLineCount,
            warnCount: res.rows.warnCount
          };
          // this.showEcharts2(
          //   [
          //     { name: '运行', value: res.rows.runCount },
          //     { name: '空闲', value: res.rows.idleCount },
          //     { name: '离线', value: res.rows.offLineCount },
          //     { name: '告警', value: res.rows.warnCount }
          //   ]
          // );
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    getTodayFirstDay() {
      const time = new Date(this.currentTime);
      var y = time.getFullYear(); // 获取年份
      var m = time.getMonth() + 1; // 获取月份
      var d = time.getDate(); // 获取当月最后一日
      m = m < 10 ? '0' + m : m; // 月份补 0
      d = d < 10 ? '0' + d : d; // 日数补 0

      return [y, m, d].join('-') + ' 00:00:00';
    },
    getTodayLastDay() {
      const time = new Date(this.currentTime);
      var y = time.getFullYear(); // 获取年份
      var m = time.getMonth() + 1; // 获取月份
      var d = time.getDate(); // 获取当月最后一日
      m = m < 10 ? '0' + m : m; // 月份补 0
      d = d < 10 ? '0' + d : d; // 日数补 0

      return [y, m, d].join('-') + ' 23:59:59';
    },
    limitMoveNum() {
      let num = 0;
      this.deviceList.forEach(item => {
        num += item.length;
      });
      return num > 3 ? 1 : 4;
    },
    async getDeviceData() {
      try {
        const res = await deviceRealLiveGetPageByDevice(
          {
            startTime: this.getTodayFirstDay(),
            endTime: this.getTodayLastDay()
          },
          {
            secretKey: this.kanbanConfig.key,
            conditionJson1: [
              {
                column: 'isProductionDevice',
                operator: 1,
                ptype: 'int',
                type: 'eq',
                value: 1
              }
            ],
            current: 1,
            size: 99999999
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          const data = res.rows || [];
          console.log(res);
          if (data.length > 0) {
            const temp = data.reduce((total, current) => {
              const name = this.getLineName(current);
              total[name]
                ? total[name].push({ ...current, lineName: name })
                : (total[name] = [{ ...current, lineName: name }]);
              return total;
            }, {});
            const threeGroud = [];
            const twoGroud = Object.values(temp);
            twoGroud.forEach(item => {
              threeGroud.push(this.dataDeal(item));
            });
            this.deviceList = threeGroud;
            console.log(threeGroud);
          }
        }
      } catch (error) {
        console.log(error);
        // showErrorNotify(TITLE.NOTIFY, error);
      }
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return (
        decodeURIComponent(
          (new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(
            url
          ) || ['', ''])[1].replace(/\+/g, '%20')
        ) || null
      );
    },
    // 获取产线名称
    getLineName(row) {
      var lineName = '';
      if (row.productionLine) {
        lineName = row.productionLine;
      } else if (row.workshop) {
        lineName = row.workshop;
      } else if (row.factory) {
        lineName = row.factory;
      } else {
        lineName = '其他';
      }
      return lineName;
    },
    dataDeal(data) {
      let newArray = [];
      if (data.length <= this.rowDeviceNum) {
        const num = this.rowDeviceNum - data.length;
        newArray = [[...data, ...new Array(num).fill({})]];
      } else {
        newArray = this.group(data, this.rowDeviceNum);
      }
      return newArray;
    },
    group(array, subGroupLength) {
      let index = 0;
      const newArray = [];
      while (index < array.length) {
        const currentArr = array.slice(index, (index += subGroupLength));
        if (currentArr.length === subGroupLength) {
          newArray.push(currentArr);
        } else {
          const num = subGroupLength - currentArr.length;
          newArray.push([...currentArr, ...new Array(num).fill({})]);
        }
      }
      return newArray;
    },
    // 看板时间
    async getDataTime() {
      try {
        const res = await getServerTime({}, {});
        this.dataTime = res.rows;
        console.log(res);
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>

